import Views from './views';
import icons from 'url:../../img/icons.svg';
class ResultView extends Views {
  _parentEl = document.querySelector('.results');
  _errorMsg = 'No recipes found.';
  _generateMarkup() {
    return this._data.map(e => this._generateMarkupPreview(e)).join('');
  }
  _generateMarkupPreview(e) {
    return `<li class="preview">
    <a class="preview__link preview__link--active" href="#${e.id}">
      <figure class="preview__fig">
        <img src="${e.img}" alt="Test" />
      </figure>
      <div class="preview__data">
        <h4 class="preview__title">${e.title}</h4>
        <p class="preview__publisher">${e.publisher}</p>
        <div class="preview__user-generated">
          <svg>
            <use href="${icons}#icon-user"></use>
          </svg>
        </div>
      </div>
    </a>
  </li>`;
  }
}

export default new ResultView();
