import { mark } from 'regenerator-runtime';
import Views from './views';
import icons from 'url:../../img/icons.svg';

class Pagination extends Views {
  _parentEl = document.querySelector('.pagination');
  clickHandler(f) {
    this._parentEl.addEventListener('click', function (e) {
      const btn = e.target.closest('.btn--inline');
      if (!btn) return;
      const goTo = +btn.dataset.goto;
      f(goTo);
    });
  }
  _generateMarkup() {
    const numResults = Math.ceil(
      this._data.result.length / this._data.resultPerPage
    );
    const page = this._data.page;
    let markUp;
    // Page 1 and there are next pages
    if (numResults > 1 && page === 1) {
      markUp = this._btnNext(page);
      return markUp;
    }
    // Page 1 and there are no other pages
    if (numResults === page && numResults === 1) {
      markUp = '';
      return markUp;
    }
    // Page 2 and there are other pages before and after it
    if (numResults > page) {
      markUp = this._btnPrev(page) + this._btnNext(page);
      return markUp;
    }
    //Last page
    if (numResults === page && numResults > 1) {
      markUp = this._btnPrev(page);
      return markUp;
    }
  }
  _btnPrev(page) {
    return `
          <button data-goto="${
            page - 1
          }" class="btn--inline pagination__btn--prev">
            <svg class="search__icon">
              <use href="${icons}#icon-arrow-left"></use>
            </svg>
            <span>Page ${page - 1}</span>
          </button>`;
  }
  _btnNext(page) {
    return `
    <button data-goto='${page + 1}' class="btn--inline pagination__btn--next">
    <span>Page ${page + 1}</span>
    <svg class="search__icon">
       <use href="${icons}#icon-arrow-right"></use>
    </svg>
    </button>`;
  }
}
export default new Pagination();
