'use strict';
import NewRecipe from '../js/views/recipeView';
import NewSearch from '../js/views/searchView';
import NewResult from '../js/views/resultView';
import NewPagination from '../js/views/paginationView';
import NewBookmark from '../js/views/bookmarksView';
import * as model from './model.js';
import 'core-js/stable';
import 'regenerator-runtime/runtime';
import { any } from 'micromatch';
import Views from './views/views';
import resultView from '../js/views/resultView';

const browserslist = require('browserslist');
const { async } = require('regenerator-runtime');

const controlRecipe = async function () {
  try {
    const id = window.location.hash.slice(1);
    if (!id) return;
    NewRecipe.spinner();
    await model.loadRecipe(id);
    NewRecipe.render(model.state.recipe);
  } catch (err) {
    console.log(err);
    NewRecipe.renderError();
  }
};
const controlSearch = async function () {
  try {
    const searchKey = NewSearch.getQuery();
    if (!searchKey)
      return resultView.renderError('Use proper keywords to search!');
    NewResult.spinner();
    await model.loadSearch(searchKey);
    NewSearch.clearInput();
    NewResult.render(model.resultsPage(1));
    NewPagination.render(model.state.search);
  } catch (err) {
    NewRecipe.renderError();
  }
};
const controlPagination = function (pageDir) {
  NewResult.render(model.resultsPage(pageDir));
  NewPagination.render(model.state.search);
};
const controlServings = function (servings) {
  model.updateServings(servings);
  NewRecipe.render(model.state.recipe);
};
const controlBookmark = function () {
  if (!model.state.recipe.bookmarked) model.addBookmark(model.state.recipe);
  else model.deleteBookmark(model.state.recipe.id);
  NewRecipe.render(model.state.recipe);
  NewBookmark.render(model.state.bookmarks);
};
const init = function () {
  NewRecipe.renderHandler(controlRecipe);
  NewRecipe.renderUpdateServings(controlServings);
  NewRecipe.renderBookmark(controlBookmark);
  NewSearch.searchHandler(controlSearch);
  NewPagination.clickHandler(controlPagination);
  NewBookmark.render(model.state.bookmarks);
  AddRecipe.UploadRecipe(uploadRecipe);
};
init();
